import axios from 'axios'
import React, { useEffect, useRef, useState } from 'react'
import { Button, Container, Row, Table } from 'react-bootstrap'
import { message } from 'antd'
import ReactPaginate from 'react-paginate'

const AddWalletReq = () => {
  const [walletRequest, setWalletRequest] = useState([])
  const limit = 8
  const [pageCount, setPageCount] = useState(1)
  const currentPage = useRef(1)

  const getPaginatedData = async (page) => {
    try {
      const res = await axios.get(
        `/api/v1/masterAdmin/getAddMoneyToWalletRequests?page=${currentPage.current}&limit=${limit}`,
      )
      if (res.data.success) {
        const data = res.data.data
        if (data.results.length === 0) {
          setWalletRequest([])
        } else {
          setWalletRequest(data.results)
          setPageCount(data.totalPages) // Use total pages from the response
        }
      }
    } catch (error) {
      console.log(error)
      message.error('Something went wrong')
    }
  }

  const handlePageClick = (e) => {
    const selectedPage = e.selected + 1
    currentPage.current = selectedPage
    getPaginatedData(selectedPage) // Fetch data for the selected page
  }
  useEffect(() => {
    currentPage.current = 1
    getPaginatedData()
  }, [])

  const handleApprove = async (id) => {
    try {
      const res = await axios.patch(`/api/v1/masterAdmin/approveAddMoneyToWalletRequest/${id}`)
      if (res.data.success) {
        message.success('Approved Successfully')
        getPaginatedData()
      }
    } catch (error) {
      console.log(error)
    }
  }
  const handleReject = async (id) => {
    try {
      const res = await axios.patch(`/api/v1/masterAdmin/rejectAddMoneyToWalletRequest/${id}`)
      if (res.data.success) {
        getPaginatedData()
        message.success('Rejected Successfully')
      }
    } catch (error) {
      console.log(error)
    }
  }

  return (
    <>
      <Row className="mb-3">
        <Container fluid>
          <h3>Add to Wallet Request</h3>
          <Row>
            <Table responsive>
              <thead>
                <tr>
                  <th className="text-center">Sn</th>
                  <th className="text-center">Date</th>
                  <th className="text-center">Name</th>
                  <th className="text-center">UniqueId</th>
                  <th className="text-center">Transaction ID</th>
                  <th className="text-center">Amount</th>
                  <th className="text-center" colSpan={2}>
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                {walletRequest && walletRequest.length > 0 ? (
                  walletRequest.map((item, index) => (
                    <tr key={index}>
                      <td className="text-center">{index + 1}</td>
                      <td className="text-center">{new Date(item.date).toLocaleDateString()}</td>
                      <td className="text-center">{item.userId.name}</td>
                      <td className="text-center">{item.userId.uniqueId}</td>
                      <td className="text-center">{item.transactionId}</td>
                      <td className="text-center">{item.amount}</td>

                      <td className="text-center">
                        <Button
                          className="bg-success border-0"
                          onClick={() => {
                            handleApprove(item._id)
                          }}
                        >
                          Approve
                        </Button>
                      </td>
                      <td>
                        <Button
                          style={{
                            backgroundColor: 'red',
                            color: 'white',
                            border: 'none',
                          }}
                          onClick={() => {
                            handleReject(item._id)
                          }}
                        >
                          Reject
                        </Button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={8} className="text-center text-danger">
                      No data found
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>
          </Row>
        </Container>
      </Row>
      <Row>
        <ReactPaginate
          breakLabel="..."
          nextLabel="next >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={5}
          pageCount={pageCount}
          previousLabel="< previous"
          renderOnZeroPageCount={null}
          marginPagesDisplayed={2}
          containerClassName="pagination justify-content-center"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextClassName="page-item"
          nextLinkClassName="page-link"
          activeClassName="active"
        />
      </Row>
    </>
  )
}

export default AddWalletReq
